// @media (min-width: 576px)


@media (min-width: 768px) {
    .hidden-desktop {
      display: none; } }

@media (min-width: 992px) {
  /* Эффект при наведении кнопок */

  .btn {
    &:hover {
      background-image: linear-gradient(-18deg, darken($active, 5) 0%, darken(#2e76a0, 5) 50%, darken($active, 5) 100%); } }

  .btn-o {
    &:hover {
      background-image: none;
      background-color: rgba(#f4f4f4, 0.1); } }


  /* Эффект блока услуг */

  .services {

    &-block {

      &:hover {
        background-color: #272e3c;

        & .services-img {
          display: none; }

        & .services-img_active {
          display: inline-block; } }

      &:hover &__btn {
        display: block; }

      &:hover &__title {
        font-weight: 700;
        color: #ffffff; }

      &__btn {

        &:hover {
          background-color: #fff !important;
          color: #272e3c; } } } } }

// @media (min-width: 1200px)




@media (max-width: 1800px) {

  /* Request section */

  .request {

    &:after {
      top: 45%; } } }

@media (max-width: 1399px) {
  /* Контент слайдера чуть правее */
  .main {

    &-slider {

      &__title, &__descr, &__btn {
        margin-left: 5%; } } }



  /* Header */

  .header {


    &-logo {
      margin-right: 150px;


      &::before, &::after {
        width: 79px; }

      &::before {
        left: -91px; }

      &::after {
        right: -91px; } }


    &__btn {
      margin-left: 20px; } }


  /* Services */

  .services {

    &-block {

      &__img {
        flex-basis: 45%; }

      &__text {
        flex-basis: 55%; } } }


  /* About */

  .about {

    &-block {
      padding: 135px 0 150px 47px; } }

  /* Slider - section */

  .slider-section {

    &__title, &__descr, &__btn {
      margin-right: 3%; } }

  /* Partners */

  .partners {


    &-slider {
      max-width: 1140px; }

    &-block {

        &__img {
          height: 150px; } }

    .slick {

      &-dots {

          button {
            border: 2px solid rgba(#242830, 0.2); } } } }

  /* Request section */

  .request {

    &:after {
      width: 420px;
      height: 110px;
      top: 55%;
      background-size: 420px 110px; } }

  /* Our-services Page */

  .our-services {

    /* Sidebar */

    .sidebar {

      &-item {

        &__text {

          font-size: 15px; } } } }

  /* Modal */

  .overlay {

    .popup {
      top: 25%; } } }




@media (max-width: 1199px) {

